@charset "UTF-8";
/*
Theme Name: Nathalie Mota Photos
Author: Romain Gilles
Description: Thème personnalisé pour le site Web de la photographe Nathalie Mota
Requires at least: 6.1.1
Tested up to: 6.1.1
Requires PHP: 8.1.9
Version: 1.0
*/
@import url("https://fonts.googleapis.com/css2?family=Space+Mono&display=swap");
* {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  all: unset;
  cursor: pointer;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 0 10%;
}
.header__heading {
  width: 216px;
}
.header li {
  padding-left: 45px;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 7px 0;
  border-top: 1px solid black;
}
.footer__nav {
  justify-content: center;
  /* width: 622px; */
  /* margin: auto; */
  /* .menu {
      display: flex;
      justify-content: space-between;
  } */
}
.footer__nav li:nth-child(2) {
  padding-left: 86px;
  padding-right: 86px;
}

nav {
  display: flex;
  /* justify-content: space-between; */
  font-family: "Space Mono", monospace;
  line-height: 24px;
  text-transform: uppercase;
}
nav .menu {
  display: flex;
  /* justify-content: space-between; */
}
nav li:hover {
  font-weight: 700;
}

.modale {
  display: none; /* Hidden by default */
  /* opacity: 0.5; */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 200;
  top: 200;
  width: 250%; /* Full width */
  height: 25%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
  color: blue;
}